import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
import {
  useClassTabController,
  useClassTabManage,
} from "../../Hooks/ApiHooks/ClassManager/class-manager-api";
const ClassTabController = ({ did }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    catalog: true,
    batch_standard: true,
    attendance: true,
    leave_and_transfer: true,
    fees: true,
    timetable: true,
    checklist: true,
    behaviour: true,
    report_card: true,
    promote_student: true,
    complaint: true,
    setting: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [classTabController] = useClassTabController();
  const { classTabManage } = useClassTabManage({
    data: {
      did: did,
    },
    skip: !did,
  });

  useEffect(() => {
    if (classTabManage?.tab_manage) {
      setState({
        catalog: classTabManage?.tab_manage?.catalog,
        batch_standard: classTabManage?.tab_manage?.batch_standard,
        attendance: classTabManage?.tab_manage?.attendance,
        leave_and_transfer: classTabManage?.tab_manage?.leave_and_transfer,
        fees: classTabManage?.tab_manage?.fees,
        timetable: classTabManage?.tab_manage?.timetable,
        checklist: classTabManage?.tab_manage?.checklist,
        behaviour: classTabManage?.tab_manage?.behaviour,
        report_card: classTabManage?.tab_manage?.report_card,
        complaint: classTabManage?.tab_manage?.complaint,
        promote_student: classTabManage?.tab_manage?.promote_student,
        setting: classTabManage?.tab_manage?.setting,
      });
    }
  }, [classTabManage?.tab_manage]);

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    classTabController({
      did: did,
      tabManage: { class_tab_manage: updatedObj },
    })
      .then(() => {
        setSnackbar({
          msg: "Updated",
          open: true,
          variant: "success",
        });
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("class_tab_controller")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("class_catalog")}
              checkedToggle={state.catalog}
              onToggleHandler={(e) =>
                onSubmitFormHandler("catalog", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("hostel_tab_batch")}
              checkedToggle={state.batch_standard}
              onToggleHandler={(e) =>
                onSubmitFormHandler("batch_standard", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("attendance")}
              checkedToggle={state.attendance}
              onToggleHandler={(e) =>
                onSubmitFormHandler("attendance", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("leaves_and_transfer")}
              checkedToggle={state.leave_and_transfer}
              onToggleHandler={(e) =>
                onSubmitFormHandler("leave_and_transfer", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("class_fees")}
              checkedToggle={state.fees}
              onToggleHandler={(e) =>
                onSubmitFormHandler("fees", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("time_table")}
              checkedToggle={state.timetable}
              onToggleHandler={(e) =>
                onSubmitFormHandler("timetable", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("checklist")}
              checkedToggle={state.checklist}
              onToggleHandler={(e) =>
                onSubmitFormHandler("checklist", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("behaviour")}
              checkedToggle={state.behaviour}
              onToggleHandler={(e) =>
                onSubmitFormHandler("behaviour", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("final_report")}
              checkedToggle={state.report_card}
              onToggleHandler={(e) =>
                onSubmitFormHandler("report_card", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("complaint")}
              checkedToggle={state.complaint}
              onToggleHandler={(e) =>
                onSubmitFormHandler("complaint", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <div
              style={{
                flex: "1 1 33%",
              }}
            >
              <FormToggle
                labelHeading={t("promote_student")}
                checkedToggle={state.promote_student}
                onToggleHandler={(e) =>
                  onSubmitFormHandler("promote_student", e.target.checked)
                }
                customContainer={{
                  flex: "1 1 33%",
                }}
              />
            </div>
            <div
              style={{
                flex: "1 1 33%",
              }}
            >
              <FormToggle
                labelHeading={t("setting")}
                checkedToggle={state.setting}
                onToggleHandler={(e) =>
                  onSubmitFormHandler("setting", e.target.checked)
                }
                customContainer={{
                  flex: "1 1 33%",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default ClassTabController;
