import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import NotifyEvent from "../../Utils/Snackbar/NotifyEvent";
import {
  useSubjectTabController,
  useSubjectTabManage,
} from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
const SubjectTabController = ({ did }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    catalog: true,
    attendance: true,
    assignment: true,
    examination: true,
    academic: true,
    copo: true,
    setting: true,
    mcq: true,
  });
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [subjectTabController] = useSubjectTabController();
  const { subjectTabManage } = useSubjectTabManage({
    data: {
      did: did,
    },
    skip: !did,
  });

  useEffect(() => {
    if (subjectTabManage?.tab_manage) {
      setState({
        catalog: subjectTabManage?.tab_manage?.catalog,
        attendance: subjectTabManage?.tab_manage?.attendance,
        assignment: subjectTabManage?.tab_manage?.assignment,
        examination: subjectTabManage?.tab_manage?.examination,
        academic: subjectTabManage?.tab_manage?.academic,
        copo: subjectTabManage?.tab_manage?.copo,
        setting: subjectTabManage?.tab_manage?.setting,
        mcq: subjectTabManage?.tab_manage?.mcq,
      });
    }
  }, [subjectTabManage?.tab_manage]);

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    subjectTabController({
      did: did,
      tabManage: { subject_tab_manage: updatedObj },
    })
      .then(() => {
        setSnackbar({
          msg: "Updated",
          open: true,
          variant: "success",
        });
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("subject_tab_controller")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("catalog")}
              checkedToggle={state.catalog}
              onToggleHandler={(e) =>
                onSubmitFormHandler("catalog", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("attendance")}
              checkedToggle={state.attendance}
              onToggleHandler={(e) =>
                onSubmitFormHandler("attendance", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("assignment")}
              checkedToggle={state.assignment}
              onToggleHandler={(e) =>
                onSubmitFormHandler("assignment", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("examination")}
              checkedToggle={state.examination}
              onToggleHandler={(e) =>
                onSubmitFormHandler("examination", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("academics_teachings")}
              checkedToggle={state.academic}
              onToggleHandler={(e) =>
                onSubmitFormHandler("academic", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />

            <FormToggle
              labelHeading={t("co_mapping_and_attainment")}
              checkedToggle={state.copo}
              onToggleHandler={(e) =>
                onSubmitFormHandler("copo", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
          </div>
          <BorderBottom />
          <BorderBottom />
          <div className={style.fm_search}>
            <FormToggle
              labelHeading={t("setting")}
              checkedToggle={state.setting}
              onToggleHandler={(e) =>
                onSubmitFormHandler("setting", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <FormToggle
              labelHeading={t("mcqs")}
              checkedToggle={state.mcq}
              onToggleHandler={(e) =>
                onSubmitFormHandler("mcq", e.target.checked)
              }
              customContainer={{
                flex: "1 1 33%",
              }}
            />
            <div
              style={{
                flex: "1 1 33%",
              }}
            ></div>
          </div>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default SubjectTabController;
