import React from "react";
import style from "./InstituteSetting.module.css";
import BorderBottom from "../components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import Navbar from "../../Dashboard/InsDashboard/Components/Navbar/Navbar";
import InstituteSettingTab from "./InstituteSettingTab";
import DepartmentTabController from "../../optimized/InstituteMember/InstituteDepartment/DepartmentTabController";
import { useSelector } from "react-redux";
import SubjectTabController from "../../optimized/InstituteMember/InstituteSubject/SubjectTabController";
import ClassTabController from "../../optimized/InstituteMember/InstituteClass/ClassTabController";
const InstituteSettingConfig = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const id = useSelector((state) => state.idChange.id);
  const [searchParams] = useSearchParams();
  const onGoback = () => {
    navigate(-1);
  };

  return (
    <>
      <div className={style.navbar}>
        <Navbar status="home" />
      </div>

      <div className={style.goback_container}>
        <div className={style.goback_container_text} onClick={onGoback}>
          <img src="/images/arr-left-white.svg" alt="go back icon" />
          <h6 className={style.goback_container_text_go}>{t("go_back")}</h6>
        </div>
      </div>
      {!searchParams.get("a") ? (
        <div className={style.student_from_container}>
          <h6 className={style.student_from_fill}>{t("institute_setting")}</h6>
          <BorderBottom customStyle={{ width: "100%" }} />
          <InstituteSettingTab />
        </div>
      ) : searchParams.get("a") === "department" ? (
        <div
          className={style.student_from_container}
          style={{
            border: "none",
          }}
        >
          <DepartmentTabController did={id} />
        </div>
      ) : searchParams.get("a") === "class" ? (
        <div
          className={style.student_from_container}
          style={{
            border: "none",
          }}
        >
          <ClassTabController did={id} />
        </div>
      ) : searchParams.get("a") === "subject" ? (
        <div
          className={style.student_from_container}
          style={{
            border: "none",
          }}
        >
          <SubjectTabController did={id} />
        </div>
      ) : null}
    </>
  );
};

export default InstituteSettingConfig;
